const app = getApp()
export default {
	data() {
		return {
			gridList: [{
				text: "我的收藏",
				src: "../../static/collection.png",
				status: false
			},
			{
				text: "我的工单",
				src: "../../static/workorder.png",
				status: false
			},
			{
				text: "我的约伴",
				src: "../../static/friends.png",
				status: false
			}],
			pageList: [{
				text: "身份认证",
				src: "/static/auth.png",
				openType: "",
			},
			{
				text: "关于我们",
				src: "/static/about.png",
				openType: ""
			},{
				text: "推荐给好友",
				src: "/static/share.png",
				openType: "share"
			}],
			userInfo: null,
			tip: "点击授权",
		}
	},
	onShow() {
		console.log("onshow")
	},
	onLoad() {
		console.log("onload")
		// this.getUserInfoBySetting()
		if(app.globalData.userInfo){
			this.userInfo = app.globalData.userInfo
		}
	},
	methods: {
		/*列表点击事件*/
		// 点击跳转或者授权
		listClick(items, index){
			
		},
		/*九宫格点击事件*/
		// 按钮变色
		changeColor(bool, items, index){
			var that = this
			var gridList = that.gridList
			gridList[index].status = bool
			that.gridList = gridList
		},
		// 用户松开点击
		gridEndClick(items, index){
			// 按钮变回白色
			this.changeColor(false, items, index)
			// 页面跳转
			
		},
		// 用户点击态
		gridClick(items, index){
			// 按钮灰变色
			this.changeColor(true, items, index)
		},
		// 获取用户信息
		onGotUserInfo(e) {
			var that = this
		    console.log(e.detail.userInfo)
		    app.globalData.userInfo = e.detail.userInfo
			that.userInfo = app.globalData.userInfo
		    uni.login({
		      success(res){
		        uni.request({
		          url: app.globalData.host + app.globalData.apiVersion + "auth/qq_login",
		          method: "POST",
		          data: {
		            code: res.code,
		            userInfo: app.globalData.userInfo
		          },
		          success(result){
		            console.log(result)
					// console.log(that)
		          }
		        })
		      }
		    })
		}
	}
}
